export default {
	data() {
		return {
			nodes:'',
			entryInfo:{},
			collectsId: '',
			memberId: '',
		};
	},
	onLoad(options){
		// console.log(options.id)
		this.memberId = JSON.parse(uni.getStorageSync('userInfo')).id
		if(options.id){
			this.collectsId = options.id
			this.getCollects()
		}
	},
	methods:{
		// 获取详情数据
		getCollects(){
			this.$Request.get(`/appCollectsController.do?getCollects&memberId=${this.memberId}&collectsId=${this.collectsId}`)
			.then(res => {
				if(res.code == 0){
					this.entryInfo = res.data
					this.entryInfo.studyDate = res.data.studyDate.substring(0,10)
				}else{
					uni.showToast({
						title: res.info,
						icon: 'none'
					})
				}
			})
		},
		// 收藏
		clickAttention(){
			this.$Request.get(`/appAttentionController.do?takeCollectsAttention&memberId=${this.memberId}&collectsId=${this.collectsId}`)
			.then(res => {
				if(res.code == 0){
					this.entryInfo.attentionNum = this.entryInfo.attentionType == 1 ? this.entryInfo.attentionNum - 1 : this.entryInfo.attentionNum + 1
					this.entryInfo.attentionType = this.entryInfo.attentionType == 1 ? 0 : 1
				}else{
					uni.showToast({
						title: res.info,
						icon: 'none'
					})
				}
			})
		},
		gotoUrl(url, type){
			if(type == 'card'){ // 进入训练
				uni.navigateTo({
					url: `${url}?collectsId=${this.collectsId}`
				})
			}else if(type == 'history'){ // 历史测试
				uni.navigateTo({
					url: `${url}?title=${this.entryInfo.collectsName}&collectsId=${this.collectsId}`
				})
			}else if(type == 'test') { //前往测试
				uni.navigateTo({
					url: `${url}?collectsId=${this.collectsId}`
				})
			}
		},
	}
}
